@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap);

.movieRow {
    margin-bottom: 40px;
}

.movieRow h2 {
    margin: 0 0 10px 50px;
}

.movieRow--listarea {
    overflow-x: hidden;
    margin-left: 40px;
}

.movieRow--list {
    transition: all ease 0.5s;
}

.movieRow--item {
    display: inline-block;
    width: 200px;
    cursor: pointer;
}

.movieRow--item img {
    width: 100%;
    transform: scale(0.9);
    border-radius: 4px;
    transition: all ease 0.2s;
}

.movieRow--item img:hover {
    transform: scale(1);
}

.movieRow--left, .movieRow--right {
    position: absolute;
    width: 40px;
    height: 300px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease .5s;
}

.movieRow--left {
    left: 0;
}

.movieRow--right {
    right: 0;
}

.movieRow:hover .movieRow--left, .movieRow:hover .movieRow--right {
    opacity: 1;
}

@media (max-width: 760px){
    .movieRow--left, .movieRow--right {
        opacity: 1;
    }
}
.featured {
    height: 100vh;
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 40px;
    padding-bottom: 100px;
    padding-top: 70px;
}

.featured--name {
    font-size: 60px;
    font-weight: bold;
}

.featured--info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--points {
    color: #46d369;
}

.featured--points, .featured--year, .featured--seasons {
    display: inline-block;
    margin-right: 15px;
}

.featured--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured--buttons {
    margin-top: 15px;
}

.featured--watchbutton, .featured--mylistbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
    cursor: pointer;
}

.featured--watchbutton:hover, .featured--mylistbutton:hover {
    opacity: 0.7;
}

.featured--watchbutton svg, .featured--mylistbutton svg {
    margin-right: 8px;
}

.featured--watchbutton div, .featured--mylistbutton div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.featured--watchbutton{
    background-color: #FFF;
    color: #000;
}

.featured--mylistbutton {
    background-color: #333;
    color: #FFF;
}

.featured--genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width: 760px){
    .featured {
        height: 90vh;
    }

    .featured--name {
        font-size: 40px;
    }

    .featured--info {
        font-size: 16px;
    }

    .featured--description {
        font-size: 14px;
        max-width: 100%;
        margin-right: 40px;
    }

    .featured--watchbutton, .featured--mylistbutton {
        font-size: 16px;
    }

    .featured--genres {
        font-size: 14px;
    }
}

header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
    background: transparent;
    transition: all ease 0.5s;
}

header.black {
    background: #141414;
}

.header--logo {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header--logo div {
    margin-left: 10px;
    background: #FFF;
    color: #333;
    text-transform: uppercase;
    font-weight: 900;
    height: 25px;
    padding: 0 12px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    margin-top: -3px;
}

.header--logo img {
    height: 25px;
}

.header--user {
    height: 35px;
}

.header--user img {
    height: 100%;
    border-radius: 3px;
}
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #FFF;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists {
  margin-top: -100px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

footer div {
  margin-bottom: 10px;
}

footer div img {
  margin: 10px;
}

footer a {
  text-decoration: none;
  color: #FFF;
  font-weight: bold;
}

.loading{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading img {
  width: 700px
}
.details {
  height: 100vh;
  width: 100vw;
}

.details section {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111, transparent );
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 90px;
}

.details section div {
  text-align: left;
}

.details section div h1 {
  font-size: 42pt;
}

.details section div h4 {
    max-width: 50%;
    line-height: 28px;
    font-weight: 500;
}

.details--info {
  margin-bottom: 30px;
}

.details--info h3 {
  width: 50px;
  padding: 10px;
  border-radius: 4px;
}

.details--info h3.positive{
  background: #46d369;
}

.details--info h3.negative{
  background: #d91921;
}

.details a {
  padding: 12px 20px;
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2s;
  cursor: pointer;
}

.details a div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.details a:hover {
  opacity: 0.7;
}

.details--viewtrailer {
    background-color: #FFF;
    color: #000;
}

.details--viewtrailer svg {
  margin-right: 8px;
}

.details--officialsite {
  background-color: #FFF;
  color: #000;
}

.details aside{
  width: 500px;
  height: 300px;
  position: fixed;
  bottom: 15%;
  right: 5%;
}

.details aside div {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 6px;
}

button {
  background: transparent;
  border: none;
  color: #FFF;
  cursor: pointer;
}

.video--fullscreen iframe{
    width: 100vw;
    height: 100vh;
    left: 0;
    position: fixed;
    top: 0;
    z-index: 0
}

.video--fullscreen div {
    justify-content: center;
    z-index: 1;
    position: fixed;
    left: 0;
    border-radius: 0px 4px 4px 0px;
    background: #FFF;
    
}

.video--fullscreen div svg {
  color: #000;
  margin-top: 5px;
}

.details--backbutton {
  color: #FFF;
  position: fixed;
  top: 20px;
  left: 20px;
  font-size: 16px;
  text-shadow: 1px 1px 5px #999;
}
